<template>
  <v-card class="pb-3">
    <v-card-title class="primary white--text headline">
      Property Users Directory
    </v-card-title>
    <v-layout
      justify-space-between
      pa-3
    >
      <v-flex xs5>
        <v-treeview
          :active.sync="active"
          :items="items"
          :open.sync="open"
          activatable
          active-class="primary--text"
          class="grey lighten-5 pa-3"
          open-all
          transition
        >
          <v-icon
            v-if="!item.children"
            slot="prepend"
            slot-scope="{ item, active }"
            :color="active ? 'primary' : ''"
          >fas fa-user-circle</v-icon>
        </v-treeview>
      </v-flex>
      <v-flex
        d-flex
        text-xs-center
      >
        <v-scroll-y-transition mode="out-in">
          <div
            v-if="!selected"
            class="title grey--text text--lighten-1 font-weight-light"
            style="align-self: center;"
          >
            Select a User
          </div>
          <v-card
            v-else
            :key="selected.id"
            class="pt-4 mx-auto"
            flat
            max-width="400"
          >
            <v-card-text>
              <v-avatar
                size="88"
              >
                <v-img
                  :src="selected.avatar"
                  class="mb-4"
                ></v-img>
              </v-avatar>
              <h3 class="headline mb-2">
                {{ selected.name }}
              </h3>
              <div class="secondary--text subheading font-weight-bold">{{ selected.username }}</div>
            </v-card-text>
            <v-divider></v-divider>
            <v-layout
              tag="v-card-text"
              text-xs-left
              wrap
            >
              <v-flex tag="strong" xs5 text-xs-right mr-3 mb-2>Role:</v-flex>
              <v-flex>{{ selected.permission }}</v-flex>
              <v-flex tag="strong" xs5 text-xs-right mr-3 mb-2>Email:</v-flex>
              <v-flex>
                <a :href="`//${selected.email}`" target="_blank">{{ selected.email }}</a>
              </v-flex>
              <v-flex tag="strong" xs5 text-xs-right mr-3 mb-2>Phone:</v-flex>
              <v-flex>{{ selected.phone }}</v-flex>
            </v-layout>
          </v-card>
        </v-scroll-y-transition>
      </v-flex>
    </v-layout>
    <v-btn class="ml-5"
        color="primary"
    >
        <v-icon class="mr-2"
            size="18"
        >
            fas fa-user-plus
        </v-icon>
    Invite Users
    </v-btn>
  </v-card>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

export default {
  name: 'GuestUsers',
  data: () => ({
    active: [],
    open: [],
    dialog: false,
  }),

  computed: {
    ...mapState([
      'user',
    ]),

    items() {
      return [
        {
          name: 'Users',
          children: this.user.propertyUsers,
        },
      ];
    },
    selected() {
      if (!this.active.length) return undefined;

      const id = this.active[0];

      return this.user.propertyUsers.find(user => user.id === id);
    },
  },
  methods: {
    ...mapMutations({
      addUser: 'ADD_USER',
    }),
  },
};
</script>
